export const defaults = {
  dev: {
    allowFullscreen: true,
    allowPreventContextMenu: true,
    allowTouchPreventDefault: true,
    allowKeyboardPreventDefault: true,
    allowWarnOnReload: true
  }
}

export default class Config {
  public dev: {
    allowFullscreen: boolean;
    allowPreventContextMenu: boolean;
    allowTouchPreventDefault: boolean;
    allowKeyboardPreventDefault: boolean;
    allowWarnOnReload: boolean;
  };

  constructor (params: Partial<Config> = {}) {
    this.dev = { ...defaults.dev, ...params.dev };
  }
}
