import { hitCheckPointRect } from "./collision";
import Config from "./Config";
import { Point } from "./geometry";

enum DPadButtons {
  Up = 'Up',
  Lt = 'Lt',
  Dn = 'Dn',
  Rt = 'Rt',
  Center = 'Center',
  LtUp = 'LtUp',
  LtDn = 'LtDn',
  RtUp = 'RtUp',
  RtDn = 'RtDn'
}

interface DPadButtonItem {
  name: DPadButtons;
  element: HTMLDivElement | null;
}

export interface ButtonsParams {
  config: Config;
}

export default class Buttons {
  private config: Config;

  public dPadElement: HTMLDivElement;
  public dPadButtons: DPadButtonItem[] = [
    {
      name: DPadButtons.LtUp,
      element: null
    },
    {
      name: DPadButtons.Up,
      element: null
    },
    {
      name: DPadButtons.RtUp,
      element: null
    },
    {
      name: DPadButtons.Lt,
      element: null
    },
    {
      name: DPadButtons.Center,
      element: null
    },
    {
      name: DPadButtons.Rt,
      element: null
    },
    {
      name: DPadButtons.LtDn,
      element: null
    },
    {
      name: DPadButtons.Dn,
      element: null
    },
    {
      name: DPadButtons.RtDn,
      element: null
    }
  ];
  
  public actionBtnsElement: HTMLDivElement;
  public actionBtnIElement: HTMLDivElement;
  public actionBtnIIElement: HTMLDivElement;

  public powerBtnElement: HTMLDivElement;
  public settingsBtnElement: HTMLDivElement;

  public readonly isLtUpPressed: boolean = false;
  public readonly isUpPressed: boolean = false;
  public readonly isRtUpPressed: boolean = false;
  public readonly isLtPressed: boolean = false;
  public readonly isRtPressed: boolean = false;
  public readonly isLtDnPressed: boolean = false;
  public readonly isDnPressed: boolean = false;
  public readonly isRtDnPressed: boolean = false;

  constructor (params: ButtonsParams) {
    this.config = params.config;

    this.dPadElement = document.querySelector('#dPad')!;

    this.dPadButtons.forEach((item) => {
      item.element = document.querySelector(`#${item.name}`);
    });

    this.actionBtnsElement = document.querySelector('#actionBtns')!;
    this.actionBtnIElement = document.querySelector('#actionBtnI')!;
    this.actionBtnIIElement = document.querySelector('#actionBtnII')!;

    this.powerBtnElement = document.querySelector('#powerBtn')!;
    this.settingsBtnElement = document.querySelector('#settingsBtn')!;
  }
  
  private setActiveDPadElements (item: DPadButtonItem): void {
    if (item.name === DPadButtons.LtUp) {
      this.dPadButtons.find((item) => item.name === DPadButtons.Lt)?.element?.classList.add('active');
      this.dPadButtons.find((item) => item.name === DPadButtons.Up)?.element?.classList.add('active');
    } else if (item.name === DPadButtons.RtUp) {
      this.dPadButtons.find((item) => item.name === DPadButtons.Rt)?.element?.classList.add('active');
      this.dPadButtons.find((item) => item.name === DPadButtons.Up)?.element?.classList.add('active');
    } else if (item.name === DPadButtons.LtDn) {
      this.dPadButtons.find((item) => item.name === DPadButtons.Lt)?.element?.classList.add('active');
      this.dPadButtons.find((item) => item.name === DPadButtons.Dn)?.element?.classList.add('active');
    } else if (item.name === DPadButtons.RtDn) {
      this.dPadButtons.find((item) => item.name === DPadButtons.Rt)?.element?.classList.add('active');
      this.dPadButtons.find((item) => item.name === DPadButtons.Dn)?.element?.classList.add('active');
    } else {
      item.element?.classList.add('active');
    }
  }

  public powerOn (): void {
    this.powerBtnElement.classList.remove('off');
    this.powerBtnElement.classList.add('on');
  
    this.dPadElement.addEventListener('touchstart', (event) => {
      this.setActiveDPadElements(this.dPadButtons.find((item) => item.name === (event.target as HTMLElement).id)!);
    }, false);
    this.dPadElement.addEventListener('touchmove', (event: TouchEvent) => {
      if (this.config.dev.allowTouchPreventDefault) {
        event.preventDefault();
      }

      this.dPadButtons.forEach((item) => {
        item.element?.classList.remove('active');
      });
  
      const firstTouch = event.touches[0];
      
      this.dPadButtons.forEach((item) => {
        if (hitCheckPointRect(new Point(firstTouch.pageX, firstTouch.pageY), item.element!.getBoundingClientRect())) {
          this.setActiveDPadElements(item);
        }
      });
    }, false);
    this.dPadElement.addEventListener('touchend', (event) => {
      this.dPadButtons.forEach((item) => {
        item.element?.classList.remove('active');
      });
    }, false);
  
    this.actionBtnsElement.addEventListener('touchstart', (event) => {
      (event.target as HTMLElement).classList.add('active');
    }, false);
    this.actionBtnsElement.addEventListener('touchmove', (event: TouchEvent) => {
      if (this.config.dev.allowTouchPreventDefault) {
        event.preventDefault();
      }

      const firstTouch = event.touches[0];

      this.actionBtnIElement.classList.remove('active');
      this.actionBtnIIElement.classList.remove('active');
      
      if (hitCheckPointRect(new Point(firstTouch.pageX, firstTouch.pageY), this.actionBtnIElement.getBoundingClientRect())) {
        this.actionBtnIElement.classList.add('active');
      } else if (hitCheckPointRect(new Point(firstTouch.pageX, firstTouch.pageY), this.actionBtnIIElement.getBoundingClientRect())) {
        this.actionBtnIIElement.classList.add('active');
      }
    }, false);
    this.actionBtnsElement.addEventListener('touchend', (event) => {
      (event.target as HTMLElement).classList.remove('active');
    }, false);
  }
}
